:root {
  --primary-color: #f2f2f2;
  --secondary-color: black;
  --text-color: white;
  --font-size: 30px;
}

*, body, html {
  font-smooth: always;
  margin: 0;
  padding: 0;
}

::selection {
  color: #fff;
  background: #000;
}

body {
  cursor: default;
  flex-direction: column;
  min-height: 100dvh;
  display: flex;
}

nav {
  background-color: var(--secondary-color);
  min-height: 8dvh;
  color: var(--text-color);
  justify-content: space-between;
  align-items: center;
  padding: 0 1dvw;
  display: flex;
}

nav h1 {
  font-size: 2dvw;
}

nav ul li {
  cursor: pointer;
  list-style: none;
  transition: transform .2s ease-in;
}

nav ul li a {
  color: var(--text-color);
  font-size: 1dvw;
  text-decoration: none;
}

nav ul li:after {
  content: "";
  transform-origin: 0;
  background-color: #fff;
  width: 100%;
  height: 1px;
  margin-top: 4px;
  transition: transform .4s;
  display: block;
  transform: scaleX(0);
}

nav ul li:hover:after {
  transform: scaleX(1);
}

main {
  text-align: center;
  justify-content: center;
  width: 80%;
  margin: 2vw auto;
}

main div {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid #000;
  border-radius: 30px;
  width: 100%;
  position: relative;
}

main p {
  text-wrap: pretty;
  margin: 3rem 0 2rem;
  font-size: 2.5dvh;
}

main p span {
  color: var(--primary-color);
  letter-spacing: 1px;
  background-color: orange;
  border-radius: 4px;
  padding: 0 3px;
  display: inline-block;
}

.space-filler {
  flex: 1;
  min-height: 30dvh;
}

footer {
  box-sizing: border-box;
  background-color: var(--secondary-color);
  width: 100dvw;
  min-height: 15dvh;
  margin-top: 3rem;
}

@keyframes rotate {
  0% {
    transform: rotateX(0);
  }

  10% {
    transform: rotateX(90deg);
  }

  20% {
    transform: rotateX(0);
  }
}

canvas {
  z-index: -10;
  background-color: #fff;
  width: 100dvw;
  height: 100dvh;
  margin: auto;
  position: absolute;
}

@media screen and (width <= 600px) {
  nav h1 {
    font-size: 6dvw;
  }

  nav ul li a {
    color: var(--text-color);
    font-size: 3dvw;
    text-decoration: none;
  }

  main p {
    text-wrap: pretty;
    margin: 3rem 0 2rem;
    font-size: 4dvw;
  }

  main div {
    margin-top: 3dvh;
  }

  .title {
    font: 6dvw;
  }
}
